<template>
  <div class="hello" style="padding: 0 8px">
    <div style="height: 5rem;"></div>
    <div class="logo"><img :src="imgSrc.bg" style="width: 50%;height: 99.5%"></div>
    <p class="h1Class">{{ msg }}</p>
    <p class="titlesSmall">{{ msgPhone }}</p>
    <div style="clear:both"></div>
    <div style="padding: 5rem 1rem 1.5rem 1rem;box-sizing:border-box;width: 100%">

      <el-row style="width: 100%">
        <el-col :span="24">
          <el-form>
            <el-form-item>
              <el-col :span="15">
                <el-input v-model="phoneNumber" type="number" placeholder="" auto-complete="off"></el-input>
              </el-col>
              <el-col :span="8" :push="1">
                <el-button type="primary" :disabled="YZdisabled" @click.native="handerClick()"
                           style="width: 100%;text-align: center"><span>{{yanzheng}}</span></el-button>
              </el-col>
            </el-form-item>
            <el-form-item>
              <el-col :span="24">
                <el-input type="text" v-model="phoneNumberValue" placeholder="" auto-complete="off"></el-input>
              </el-col>
            </el-form-item>
          </el-form>
        </el-col>
        <el-button type="primary" @click.native="handerSubmit" style="width: 100%;margin-top: 1rem"></el-button>
      </el-row>
    </div>

    <mt-popup
      v-model="errorModel"
      class="imageClassSuccess"
      closeOnClickModal="true">
      <div @click.native="closeClick"
           style="width: 2rem;height: 2rem;text-align: center;margin:1.5rem auto 0.3rem auto;">
        <img width="150" :src="imgSrc.bg2" class="alertImages"/>
      </div>
      <div style="clear:both;"></div>
      <div v-if="noneModel">
        <div style="font-size:1.2rem; width: 5rem;height: 1.5rem;text-align: center;margin:0.1rem auto 0.3rem auto;">
          <span> </span>
        </div>
        <div style="font-size:1.2rem;width: 12rem;height: 1.5rem;text-align: center;margin:0.1rem auto 0.3rem auto;">
          <span></span>
        </div>
        <div style="font-size:1.2rem;width: 6rem;height: 1.5rem;text-align: center;margin:0.2rem auto 0.3rem auto;">
          <span>HR</span>
        </div>
      </div>
      <div v-if="alertMessageShow" style="margin-top: 1.5rem">{{alertMessage}}</div>
    </mt-popup>
  </div>
</template>

<script>
  import {Navbar, TabItem, Toast, MessageBox, Popup, Indicator} from 'mint-ui';

  import ManyCompany from "@/components/register/manyCompany"
  import Vue from 'vue'

  let timer1 = null;

  export default {

    name: 'hello',
    data() {
      return {
        imgSrc: {
          bg: require('../assets/indexLogo.png'),
          bg2: require('../assets/ico_error.png'),
        },
        msg: ' EHR SAAS ',
        msgPhone: '!',
        phoneNumber: '',
        phoneNumberValue: '',//
        sumSearch: '',
        sumSearchUid: [],
        isCompany: '',
        yanzheng: '',//
        YZdisabled: false,
        errorModel: false, //
        noneModel: false, //
        alertMessage: '', //
        alertMessageShow: true,
      }
    },
    created: function () {
      Indicator.open('...');
      //====openidstart  6-2 ---------
      let _href = window.location.href;
      console.log(window.location.href);
      function getUrlParam(url, name) { //
        let reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
        let r = url.substring(url.indexOf('?') + 1).match(reg);
        if (r !== null) return unescape(r[2]);
        return null;
      }

      let openID = getUrlParam(_href, "openid");
      if (openID === null) {
        let path = '/api/v1.0/wechat';
        let protocol = location.protocol;
        let hostname = location.hostname;
        window.location.href = `${protocol}//${hostname}${path}`;
        return;
      }
      this.setCookie('openId', openID, 365);
      this.handerList();        // 
      //====openid ====
    },

    methods: {
      handerClick(){
        //length
        function GetLength(str) {
          ///<summary>21</summary>
          ///<param name="str"></param>
          let realLength = 0, len = str.length, charCode = -1;
          for (let i = 0; i < len; i++) {
            charCode = str.charCodeAt(i);
            if (charCode >= 0 && charCode <= 128) realLength += 1;
            else realLength += 2;
          }
          return realLength;
        };
        if (GetLength(this.phoneNumber) === 11) {
          let number = {
            phone: this.phoneNumber,
          };
          this.$http.get('/api/v1.0/client/code/' + number.phone).then(response => {
            //
            if (response.body.code === 200) {
              //
              let self = this;
              self.YZdisabled = true;
              self.yanzheng = '(30)';
              let countdown = 30;
              if (timer1) clearInterval(timer1); //
              function settime() {
                if (countdown === 0) {
                  self.YZdisabled = false;
                  self.yanzheng = "";
                  clearInterval(timer1);
                  return;
                } else {
                  countdown--;
                  self.yanzheng = "(" + countdown + ")";
                }
              }

              //
              timer1 = setInterval(settime, 1000);
              this.setCookie('iphoneNumber', this.phoneNumber, 365);
            } else if (response.body.code === 500) {
              this.errorModel = true;
              this.noneModel = true;
              this.alertMessageShow = false; //false
              return;
            }
          }, response => {
            console.log('error callback');
          });

        } else {
          this.alertMessageShow = true;
          this.alertMessage = '';
          this.errorModel = true;
          this.noneModel = false; //
        }
      },
      handerSubmit(){
        let bindingObj = {
          "code": this.phoneNumberValue,
          "phone": this.phoneNumber,
        };
        this.$http.post('/api/v1.0/client/bind', bindingObj).then(response => { //
          if (response.body.code === 200) {
            this.$http.get('/api/v1.0/client/findCompanies/' + this.phoneNumber).then(response => {
              if (response.body.code === 200) {
                this.sumSearchUid = response.body.result;
                if (response.body.result.length === 1) {
                  //1 signCard 
                  let param = {
                    "companyUid": this.sumSearchUid[0].uid,
                  };
                  this.$http.post('/api/v1.0/client/chooseCompany', param).then(response => { //
                    if (response.body.code === 200) {
                      this.$router.push({path: '/signCard'});
                    }
                  }, response => {
                    console.log('error callback');
                  });
                } else {
                  this.handerCome(); //
                }
              } else {
                this.alertMessageShow = true;
                this.errorModel = true;
                this.noneModel = false; //
                this.alertMessage = response.body.message;
                return;
              }
            }, response => {
              console.log('error callback');
            });
          } else {
            this.alertMessageShow = true;
            this.errorModel = true;
            this.noneModel = false; //
            this.alertMessage = response.body.message;
            return;
          }
        }, response => {
          console.log('error callback');
        });
      },
      handerList: function (number) {
        this.$http.post('/api/v1.0/client/checkStaffWechat').then(response => { //
          if (!response.body.result) {
            Indicator.close();
          }
          if (number == 1) {//
            if (response.body.code === 500) {
//            MessageBox('', response.body.message);
              this.alertMessageShow = true;
              this.errorModel = true;
              this.noneModel = false; //
              this.alertMessage = response.body.message;
              return;

            }
          }

          if (response.body.code === 200) {
            let isbing = response.body.result.phone;
            this.setCookie('iphoneNumber', isbing, 365);//manyCompany
            //signCard  
            let number = {
              phone: isbing,
            };
            this.$http.get('/api/v1.0/client/findCompanies/' + number.phone).then(response => { //
              console.log('data' + response.body.result);
              //  ?/
              if (!response.body.result) {
                Indicator.close();
              }
              if (response.body.result) {
                this.sumSearchUid = response.body.result;

                if (response.body.result.length === 1) {
                  //1 signCard 
                  let param = {
                    "companyUid": this.sumSearchUid[0].uid,
                  };
                  this.$http.post('/api/v1.0/client/chooseCompany', param).then(response => { //
                    if (response.body.code === 200) {
                      this.$router.push({path: '/signCard'});
                    }
                    Indicator.close();//
                  }, response => {
                    console.log('error callback');
                  });
                } else {
                  this.handerCome(); //
                }
              }
              //
              if (response.body.code === 500) {
                this.isCompany = true;
              }
            }, response => {
//              console.log('findCompanies error callback');
            });
            //signCard  
          } else if (response.body.code === 1001) {
//              MessageBox('', response.body.message);
            this.alertMessageShow = true;
            this.errorModel = true;
            this.noneModel = false; //
            this.alertMessage = response.body.message;
            return;
          }
        }, response => {
          console.log('error callback');
        });
      },
      handerCome: function () {
        Indicator.close();//
        this.$router.push({path: '/ManyCompany'});
      },
      changeCompany(){
        let param = {
          "companyUid": this.sumSearchUid.UID,
        };
        this.$http.post('/api/v1.0/client/chooseCompany', param).then(response => {
          if (response.body.code === 200) {
            this.$router.push({path: '/signCard'});
          }
        }, response => {
          console.log('error callback');
        });
      },
      closeClick(){
        this.errorModel = false;
      }
    },
    components: {}
  }
</script>

<style scoped>
  .imageClassSuccess {
    width: 16rem;
    height: 12rem;
    border-radius: 4px;
  }

  .alertImages {
    width: 100%;
    height: 100%;
    display: block;
    text-align: center;
  }

  .hello {
    background: #ffffff;
    height: 100vh;
  }

  .logo {
    width: 100%;
    height: 3rem;
  }

  .insterInput input {
    height: 2.6rem;
    width: 100%;
  }

  .verification span {
    font-size: 0.7rem;
  }

  .verification button {
    width: 98%;
    height: 100%;
  }

  .verification label span {
    display: block;
  }

  .inputValidation input {
    width: 95%;
    height: 2.6rem;
  }

  .confirmBinding button {
    width: 97%;
  }

  p {
    font-weight: 600;
    font-size: 1.2rem;

  }

  .h1Class {
    font-size: 1.4rem;
  }

  .titlesSmall {
    font-size: 1.2rem;
  }
</style>



